import React, { useState, useRef } from 'react';
import ButtonText from '../../button-text';
import ButtonGroup from '../../button-group';
import Col from '../../col';
import Grid from '../../grid';
import Row from '../../row';
import Select from '../../select';
import SelectGroup from '../../select-group';
import SelectGroupDelimiter from '../../select-group-delimiter';
import Typography from '../../typography';

const SelectExample = () => {
  const [stateOne, setStateOne] = useState({
    valid: false,
    validated: false,
    value: ''
  });

  const refOneValidation = useRef();

  const handleStateOne = e => {
    setStateOne({
      ...stateOne,
      validated: false,
      value: e.target.value
    });
  };

  const handleStateOneSubmit = () => {
    const valid = stateOne.value && stateOne.value.length > 0;

    setStateOne({
      ...stateOne,
      valid,
      validated: true
    });

    if (!valid) {
      refOneValidation.current.focus();
    }
  };

  const handleStateOneReset = () => {
    setStateOne({
      valid: false,
      validated: false,
      value: ''
    });

    refOneValidation.current.focus();
  };

  const [stateTwo, setStateTwo] = useState({
    day: '',
    month: '',
    showError: false,
    valid: false,
    validDay: false,
    validMonth: false,
    validYear: false,
    validated: false,
    year: ''
  });

  const refTwoValidationDay = useRef();
  const refTwoValidationMonth = useRef();
  const refTwoValidationYear = useRef();

  const handleStateTwo = () => {
    const day = String(refTwoValidationDay.current.value);
    const month = refTwoValidationMonth.current.value;
    const year = String(refTwoValidationYear.current.value);

    const validDay = day.length > 0;
    const validMonth = month.length > 0;
    const validYear = year.length > 0;

    setStateTwo({
      ...stateTwo,
      day,
      month,
      showError: false,
      validDay,
      validMonth,
      validYear,
      validated: false,
      year
    });
  };

  const handleStateTwoSubmit = () => {
    const test = stateTwo.validDay && stateTwo.validMonth && stateTwo.validYear;
    const test2 = !stateTwo.validDay && !stateTwo.validMonth && !stateTwo.validYear;

    setStateTwo({
      ...stateTwo,
      showError: test2,
      valid: test,
      validated: true
    });

    // @TODO: focus on first invalid select
    if (!stateTwo.valid) {
      if (!stateTwo.validMonth) {
        refTwoValidationMonth.current.focus();
      } else if (!stateTwo.validDay) {
        refTwoValidationDay.current.focus();
      } else if (!stateTwo.validYear) {
        refTwoValidationYear.current.focus();
      }
    }
  };

  const handleStateTwoReset = () => {
    setStateTwo({
      day: '',
      month: '',
      showError: false,
      valid: false,
      validDay: false,
      validMonth: false,
      validYear: false,
      validated: false,
      year: ''
    });

    refTwoValidationMonth.current.focus();
  };

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Select</Typography>
          <Typography>Select takes all native React props directly, (e.g. onChange, etc)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            label="Card Type"
            defaultValue=""
          >
            <option value="" disabled hidden />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select with preselection</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            label="Card Type"
            defaultValue="signature_card"
          >
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select with validation</Typography>
          <Typography variant="caption">*=required</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            errorText="Error: A card type is required"
            helpText={stateOne.valid ? "Successfully selected card type" : "Please select a card type"}
            invalid={stateOne.validated && !stateOne.valid}
            label="Card Type *"
            onChange={handleStateOne}
            ref={refOneValidation}
            required
            showErrorText={!stateOne.valid}
            showHelpText={Boolean(!stateOne.validated || stateOne.valid)}
            value={stateOne.value}
          >
            <option value="" disabled hidden />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <ButtonGroup>
            <ButtonText
              colorScheme="secondary"
              onClick={handleStateOneReset}
            >
              Reset
            </ButtonText>
            <ButtonText
              onClick={handleStateOneSubmit}
            >
              Validate
            </ButtonText>
          </ButtonGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select Group with validity states</Typography>
          <Typography variant="caption">*=required</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <SelectGroup
            errorText="Error: Please provide a valid Date of Birth"
            helpText={stateTwo.valid ? "Successfully selected date of birth" : "Please fill out your Date of Birth info field"}
            invalid={!stateTwo.valid && stateTwo.validated}
            legend="Date of Birth"
            showErrorText={stateTwo.showError}
            showHelpText={Boolean(!stateTwo.validated || stateTwo.valid)}
          >
            <Select
              errorText="Error: Empty month"
              invalid={!stateTwo.validMonth && stateTwo.validated}
              label="Month *"
              onChange={handleStateTwo}
              ref={refTwoValidationMonth}
              required
              showErrorText={!stateTwo.validMonth && !stateTwo.showError}
              value={stateTwo.month}
            >
              <option value="" disabled hidden />
              {
                [
                  'January',
                  'February',
                  'March',
                  'April',
                  'May',
                  'June',
                  'July',
                  'August',
                  'September',
                  'October',
                  'November',
                  'December'
                ].map((e, i) => (
                  <option key={i} value={e.substring(0, 3).toLowerCase()}>{e}</option>
                ))
              }
            </Select>
            <SelectGroupDelimiter> / </SelectGroupDelimiter>
            <Select
              errorText="Error: Empty day"
              invalid={!stateTwo.validDay && stateTwo.validated}
              label="Day *"
              onChange={handleStateTwo}
              ref={refTwoValidationDay}
              required
              showErrorText={!stateTwo.validDay && !stateTwo.showError}
              value={stateTwo.day}
            >
              <option value="" disabled hidden />
              {
                Array
                  .from(new Array(31), (x, i) => (i + 1))
                  .map((e, i) => (
                    <option key={i} value={e}>{e}</option>
                  ))
              }
            </Select>
            <SelectGroupDelimiter> / </SelectGroupDelimiter>
            <Select
              errorText="Error: Empty year"
              invalid={!stateTwo.validYear && stateTwo.validated}
              label="Year *"
              onChange={handleStateTwo}
              ref={refTwoValidationYear}
              required
              showErrorText={!stateTwo.validYear && !stateTwo.showError}
              value={stateTwo.year}
            >
              <option value="" disabled hidden />
              {
                Array
                  .from(new Array((new Date()).getFullYear() - 1900), (x, i) => (i + 1900))
                  .map((e, i) => (
                    <option key={i} value={e}>{e}</option>
                  ))
              }
            </Select>
          </SelectGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <ButtonGroup>
            <ButtonText
              colorScheme="secondary"
              onClick={handleStateTwoReset}
            >
              Reset
            </ButtonText>
            <ButtonText
              onClick={handleStateTwoSubmit}
            >
              Validate
            </ButtonText>
          </ButtonGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select (ReadOnly)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            label="Select Card"
            readOnly
            value="Visa Signature Card"
          >
            <option value="" disabled hidden />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select (Disabled)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            disabled
            label="Select Card"
            value="signature_card"
          >
            <option value="" disabled />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
    </Grid>
  );
};

export default SelectExample;
